import React from 'react';
import Button from '@material-ui/core/Button'
import Box from '@material-ui/core/Box'
import { IHistory } from '../../../interfaces/IHistory';
// import './Task.css';

interface Props {
    task: IHistory
    actHandler: (taskName: string) => void
    pinHandler: (taskName: string) => void
}

export function Task(props: Props) {
    const { task, actHandler, pinHandler } = props
    return (
        <Box className="task" display="flex" justifyContent="space-between">
            <Button onClick={() => { return actHandler(task.taskName) }}>
                <span className="material-icons">play_arrow</span>
            </Button>
            <Button onClick={() => { return pinHandler(task.taskName) }} color={task.isPinned ? "primary" : "default"}>
                <span className="material-icons">push_pin</span>
            </Button>
            <Box flexGrow="1" margin="auto"><Box>{task.taskName}</Box></Box>
        </Box>
    );
}
